'use strict';

/**
* Maximum length of a typed array.
*
* @module @stdlib/constants-array-max-typed-array-length
*
* @example
* import MAX_TYPED_ARRAY_LENGTH from '@stdlib/constants-array-max-typed-array-length';
* // returns 9007199254740991
*/

// MAIN //

/**
* Maximum length of a typed array.
*
* ```tex
* 2^{53} - 1
* ```
*
* @constant
* @type {number}
* @default 9007199254740991
*/
var MAX_TYPED_ARRAY_LENGTH = 9007199254740991;


// EXPORTS //

export default MAX_TYPED_ARRAY_LENGTH;
